  overflow: hidden; }
  #profile .profile-wrap .pic-wrap .profile-pic {
    position: relative; }
    #profile .profile-wrap .pic-wrap .profile-pic input[type="file"] {
      display: none; }
    #profile .profile-wrap .pic-wrap .profile-pic .profile-current, #profile .profile-wrap .pic-wrap .profile-pic .profile-new {
      border: 3px solid #fff;
      border-radius: 50%;
      overflow: hidden; }
    #profile .profile-wrap .pic-wrap .profile-pic .profile-new {
      position: absolute;
      top: 0;
      right: 0;
      background: #fff;
      border: 0;
      display: block;
      width: 42px;
      height: 42px; }
      #profile .profile-wrap .pic-wrap .profile-pic .profile-new img {
