#share .logo {
  width: 50%; }

#share .pic-share-wrap {
  background: url(src/images/frame.png) no-repeat center center;
  display: block;
  width: 100%;
  height: auto;
  background-size: contain; }
  #share .pic-share-wrap img {
    padding: 50px;
