.app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  min-height: 580px;
  overflow: hidden; }

.app--title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 65px;
  text-transform: uppercase;
  transform: translateY(0%);
  opacity: 1;
  transition: transform .2s, opacity .2s; }

.app--title.hidden {
  transform: translateY(-100%);
  opacity: 0; }

.app--cards {
  position: relative;
  width: 270px;
  height: 295px;
  overflow: initial; }

.app--element {
  position: absolute;
  top: 0px;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(src/images/frame.png);
  box-sizing: border-box black;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.5); }

.pic-name, .pic-detail {
  width: 270px;
  position: absolute;
  bottom: -50px;
  font-size: 16px; }

.pic-detail {
  bottom: -70px;
  font-size: 12px; }

.app--element:not([isMovable="true"]) {
  transition: transform .2s, opacity .2s; }

.app--element img {
  width: 80%;
  height: auto; }

.app--buttons {
  display: flex;
  flex-direction: row;
  width: 80%;
  justify-content: space-between;
  margin-top: 120px;
  padding: 0 20px;
  box-sizing: border-box;
  transform: translateY(0%);
  opacity: 1;
  transition: transform .2s, opacity .2s; }

.app--buttons.hidden {
  transform: translateY(100%);
  opacity: 0; }

.app--accept, .app--cancel, .app--sp {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  border: 0;
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 1;
  transform: scale(1);
  transition: opacity .2s, transform .2s; }
  .app--accept p, .app--cancel p, .app--sp p {
    display: block;
    width: 100px; }

.app--accept:hover, .app--cancel:hover, .app--sp:hover {
  opacity: 1;
  transform: scale(1.2); }

.app--restart {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 10px;
  border: 3px solid black;
  padding: 5px 35px;
  opacity: 1;
  transition: opacity .3s;
  transition-delay: .15s;
  display: none; }

.app--restart.hidden {
  opacity: 0; }

.switchSmall {
  display: inline-block;
  float: right;
  margin-top: 10px; }

.switchSmall input {
  display: none; }

.switchSmall small {
  display: inline-block;
  width: 42px;
  height: 24px;
  background: #dedede;
  border-radius: 30px;
  position: relative;
  cursor: pointer; }

.switchSmall small:before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  background: #fff;
  border-radius: 50%;
  top: 0px;
  left: 0px;
  transition: .3s;
  box-shadow: -3px 0 3px rgba(0, 0, 0, 0.1); }

.switchSmall input:checked ~ small {
  background: #a43799;
  transition: .3s; }

.switchSmall input:checked ~ small:before {
  transform: translate(16px, 0px);
  transition: .3s; }

/* start input-range */
.input-range {
  padding-top: 15px;
  padding-bottom: 15px;
  position: relative;
  max-width: 300px; }

.input-range input[type="range"].multirange {
  width: 100%; }

.input-range .range {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  padding-top: 15px;
  color: #fff;
  font-size: 12px; }

.input-range input[type="range"].multirange {
  padding: 0;
  --range-color: #ba68b9;
  /* 007cc2 */
  margin: 0;
  display: inline-block;
  vertical-align: top;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  cursor: pointer; }

.input-range input[type="range"].multirange.original {
  position: absolute;
  background: transparent;
  display: block; }

.input-range input[type="range"].multirange::-webkit-slider-runnable-track {
  background: linear-gradient(to right, #ddd, #ddd) no-repeat 0 50%/100% 20%; }

.input-range input[type="range"].multirange.original::-webkit-slider-thumb {
  position: relative;
  z-index: 2; }

.input-range input[type="range"].multirange::-webkit-slider-thumb {
  position: relative;
  z-index: 2;
  -webkit-appearance: none;
  border-radius: 15px;
  border: none;
  display: block;
  background: var(--range-color);
  height: 15px;
  width: 15px; }

.input-range input[type="range"].multirange.original::-moz-range-thumb {
  transform: scale(1);
  z-index: 1; }

.input-range input[type="range"].multirange::-moz-range-track {
  border-color: transparent; }

.input-range input[type="range"].multirange.ghost {
  position: relative;
  background: linear-gradient(to right, transparent var(--low), var(--range-color) 0, var(--range-color) var(--high), transparent 0) no-repeat 0 50%/100% 20%; }

.input-range input[type="range"].multirange.ghost::-webkit-slider-runnable-track {
  background: linear-gradient(to right, transparent var(--low), var(--range-color) 0, var(--range-color) var(--high), transparent 0) no-repeat 0 50%/100% 20%; }

.input-range input[type="range"].multirange.ghost::-moz-range-track {
  background: linear-gradient(to right, transparent var(--low), var(--range-color) 0, var(--range-color) var(--high), transparent 0) no-repeat 0 45%/100% 40%; }

/* end input-range */
